export default {
  state: () => ({
    collections: {},
  }),

  getters: {
    collections: (state) => state.collections,
  },

  mutations: {
    ADD_COLLECTION(state, data) {
      state.collections[data.constructor.name] = data;
    },

    CLEAR_COLLECTIONS(state) {
      state.collections = {};
    },
  },

  actions: {
    addCollection({ commit }, data) {
      commit("ADD_COLLECTION", data);
    },

    clearCollections({ commit }) {
      commit("CLEAR_COLLECTIONS");
    },
  },
};
