import Model from "@/models/Model";
import store from "@/store";
import FetchRequest, { SortDirection } from "@/request/FetchRequest";
import { isArray, map } from "lodash";

class Collection<TModel extends Model> {
  public items: TModel[] = [];
  protected readonly model: any = null;
  public readonly listRoute: string;
  private requestInstance: FetchRequest | undefined;

  constructor(modelClass: { new (): TModel }, listRoute: string) {
    this.model = modelClass;
    this.listRoute = listRoute;

    store.dispatch("addCollection", this);
  }

  *[Symbol.iterator]() {
    yield* this.items;
  }

  get(): TModel[] {
    return this.items;
  }

  add(item: Object): void {
    this.items.push(new this.model(item));
  }

  store(items: Object[] | null): void {
    this.items = [];

    if (isArray(items) && items.length > 0) {
      items.forEach((item) => {
        this.add(item);
      });
    }
  }

  find(id: number): TModel | undefined {
    return this.items.find((item) => item.id === id);
  }

  filter(column: string, val: any): TModel[] {
    return this.items.filter((item) => {
      return item[column] === val;
    });
  }

  edit(id: number, item: TModel): void {
    const index = this.items.findIndex((item) => item.id === id);

    this.items[index] = new this.model()(item);
  }

  remove(id: number): void {
    const index = this.items.findIndex((item) => item.id === id);

    this.items.splice(index, 1);
  }

  get count(): number {
    return this.items.length;
  }

  get empty(): boolean {
    return this.count == 0;
  }

  get ids() {
    return map(this.items, "id");
  }

  storeForRelation(item: Record<string, any>) {
    if (!this.ids.includes(item["id"])) {
      this.add(item);
    }
  }

  get request(): FetchRequest {
    if (!(this.requestInstance instanceof FetchRequest)) {
      this.requestInstance = new FetchRequest(this.listRoute);
    }

    return this.requestInstance;
  }

  get filters(): Record<string, any> {
    return this.request.filters;
  }

  get pagination(): Record<string, any> {
    return this.request.pagination;
  }

  async fetch(uri: string | null = null): Promise<Object[]> {
    const data: Object[] = await this.request.fetch(uri);
    this.store(data);
    return Promise.resolve(data);
  }

  async fetchAll(): Promise<Object[]> {
    const data: Object[] = await this.request.fetchAll();
    this.store(data);
    return Promise.resolve(data);
  }

  async fetchWithFilters(): Promise<Object[]> {
    const data: Object[] = await this.request.fetchWithFilters();
    this.store(data);
    return Promise.resolve(this.get());
  }

  setFilter(key: string, val: any) {
    this.request.setFilter(key, val);

    if (this.request.filterActive) {
      this.fetchWithFilters();
    }
  }

  setPage(page: number) {
    this.request.setPage(page);

    this.fetchWithFilters();
  }

  setLimit(limit: number) {
    this.request.setLimit(limit);

    this.fetchWithFilters();
  }

  setSort(column: string, type: SortDirection): void {
    this.request.setSort(column, type);

    this.fetchWithFilters();
  }

  activateFilters() {
    this.request.activateFilters();

    this.fetchWithFilters();
  }

  disableFilters() {
    this.request.disableFilters();

    this.fetchWithFilters();
  }
}

export default Collection;
