const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach(function (to, from, next) {
  if (to.meta && to.meta.title) {
    window.document.title = to.meta.title;
  }

  window.scrollTo(0, 0);
  next();
});

router.afterEach(async (to, from) => {
  if (to.fullPath !== from.fullPath) {
    await store.dispatch("clearCollections");
  }
});

router.beforeEach((to, from, next) => {
  let middlewares = {};

  to.matched.forEach((record) => {
    if (record.meta && record.meta.middlewares) {
      middlewares = { ...middlewares, ...record.meta.middlewares };
    }
  });

  let called = 0;

  if (!isEmpty(middlewares)) {
    for (let key in middlewares) {
      if (typeof middlewares[key] === "function") {
        called += middlewares[key](to, from, next);
      }
    }
  }

  if (called === 0) {
    return next();
  }
});

export default router;
