<template>
  <div v-if="hasSlot" key="isFeedbackHasText" class="text-xs+ text-error mt-1">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "ValidationFeedback",
  computed: {
    hasSlot() {
      return (
        this.$slots.default()[0].children &&
        typeof this.$slots.default()[0].children === "string" &&
        this.$slots.default()[0].children.length > 0
      );
    },
  },
};
</script>
