import Model from "@/models/Model";

class Role extends Model {
  public name: string | null = null;
  public is_admin: boolean = false;
  public abilities: number[] | null = [];
  public readonly users_count: number | null = null;

  constructor(data: Record<string, any> | null = null) {
    super();

    this.saveAttributes(data);
  }

  getIgnores(): string[] {
    return [...super.getIgnores(), "users_count"];
  }
}

export default Role;
