import {
  camelCase,
  has,
  includes,
  isArray,
  isEmpty,
  isObject,
  kebabCase,
  snakeCase,
  upperFirst,
} from "lodash";
import api from "@/core/Api";
import store from "@/store";
import Collection from "@/collections/Collection";
import moment, { isMoment, Moment } from "moment";

class Model {
  [key: string]: any;

  public id: number | null = null;
  public created_at: string | Moment | Date | null = null;
  public updated_at: string | Moment | Date | null = null;

  getMainRoute(): string {
    return kebabCase(this.constructor.name);
  }

  getAttributes(): string[] {
    return Object.getOwnPropertyNames(this);
  }

  getIgnores(): string[] {
    return ["created_at", "updated_at"];
  }

  getCasts(): Record<string, string> {
    return {
      created_at: "datetime",
      updated_at: "datetime",
    };
  }

  getData(): Record<string, any> {
    const data: Record<string, any> = {};

    const ignores: string[] = this.getIgnores();
    const casts: Record<string, string> = this.getCasts();

    this.getAttributes().forEach((attribute: string): void => {
      if (!includes(ignores, attribute)) {
        const val = this[attribute];

        if (val instanceof Model) {
          data[attribute] = val.getData();
        } else if (isArray(val) && val[0] instanceof Model) {
          const items: Record<string, any> = [];

          val.forEach((item: Model): void => {
            items.push(item.getData());
          });

          data[attribute] = items;
        } else if (has(casts, attribute)) {
          const cast: string = casts[attribute];

          if (cast === "date" && val instanceof moment) {
            data[attribute] = (<Moment>val).format("YYYY-MM-DD");
          }
        } else {
          data[attribute] = val;
        }
      }
    });

    return data;
  }

  saveAttributes(data: Record<string, any> | null = null): void {
    const attributes: string[] = this.getAttributes();
    const casts: Record<string, string> = this.getCasts();

    if (data && !isEmpty(data)) {
      for (const key in data) {
        if (
          isArray(data[key]) &&
          isObject(data[key][0]) &&
          has(data[key][0], "id") &&
          has(data[key][0], "pivot")
        ) {
          const ids: number[] = [];
          data[key].forEach((item: Record<string, any>): void => {
            ids.push(item.id);
          });
          this[key] = ids;
        } else if (
          isArray(data[key]) &&
          isArray(this[key]) &&
          isObject(data[key][0]) &&
          has(data[key][0], "id")
        ) {
          const model = this[`${key}Model`];
          this[key] = [];
          data[key].forEach((item: any) => {
            if (model) {
              this[key].push(new model(item));
            } else {
              this[key].push(item);
            }
          });
        } else if (
          includes(attributes, `${key}_id`) &&
          isObject(data[key]) &&
          has(data[key], "id")
        ) {
          const collection: Collection<Model> = this.getCollection(key);
          if (collection) {
            collection.storeForRelation(data[key]);
          }
        } else if (
          this[key] !== undefined &&
          includes(attributes, key) &&
          has(casts, key)
        ) {
          const cast: string = casts[key];

          if (cast === "date" || cast === "datetime") {
            this[key] = moment(data[key]);
          }
        } else if (this[key] !== undefined && includes(attributes, key)) {
          this[key] = data[key];
        }
      }
    }

    if (typeof this.afterStore === "function") {
      this.afterStore();
    }
  }

  fetch(id: number): Promise<this> {
    return new Promise((resolve): void => {
      api.get(`${this.getMainRoute()}/${id}`).then(({ data }) => {
        this.saveAttributes(data);

        resolve(this);
      });
    });
  }

  fetchFind(url: string): Promise<this> {
    return new Promise((resolve): void => {
      api.get(url).then(({ data }) => {
        this.saveAttributes(data);

        resolve(this);
      });
    });
  }

  getOwnCollection(): Collection<Model> {
    return this.getCollection(this.constructor.name);
  }

  getCollection(key: string): Collection<Model> {
    key = `${upperFirst(camelCase(key)).replace("Collection", "")}Collection`;
    let collection = store.getters.collections[key];

    if (collection === undefined) {
      const collectionsContext = require.context(
        "@/collections",
        false,
        /Collection\.ts$/
      );

      const collections = collectionsContext.keys().map((collectionPath) => {
        const collectionModule = collectionsContext(collectionPath);
        return collectionModule.default;
      });

      collection = collections.find((collection) => collection.name === key);
      if (collection) {
        collection = new collection();
      }
    }

    return collection;
  }

  hasMany(model: any, column: string | null = null): any {
    const collection = this.getCollection(model.name);
    column = column ?? `${snakeCase(this.constructor.name)}_id`;

    if (collection) {
      return collection.filter(column, this["id"]);
    }

    return null;
  }

  belongsTo(model: any, column: string | null = null): any {
    const collection = this.getCollection(model.name);
    column = column ?? `${snakeCase(model.name)}_id`;

    if (collection) {
      return collection.find(this[column]);
    }

    return null;
  }

  get createdAtLocal(): string | null {
    if (!isMoment(this.created_at)) {
      this.created_at = moment(this.created_at);
    }

    if (this.created_at.isValid()) {
      return this.created_at.format("LL");
    }

    return null;
  }
}

export default Model;
