import { createApp } from "vue";
import router from "./router";
import store from "./store";
import i18n from "./lang/i18n";
import moment from "moment";
import Toast from "vue-toastification";

// Config
import { APP_LOCALE } from "@/core/Config";

// Lineone template files
import "./css/app.css";
import "./js/lineone.js";
// Component style files
import "v-calendar/style.css";

// App
import AppWrapper from "./AppWrapper.vue";

// Global Components
import VMask from "@/components/VMask.vue";
import BreadcrumbList from "@/components/BreadcrumbList/BreadcrumbList.vue";
import VueSelect from "vue-select";
import ValidationFeedback from "@/components/ValidationFeedback.vue";
import VModal from "@/components/VModal.vue";
import DataTable from "@/components/DataTable/DataTable.vue";
import VIcon from "@/components/VIcon/VIcon.vue";
import VButton from "@/components/VButton.vue";

// Localization
require(`moment/locale/${APP_LOCALE}`);
moment().locale(APP_LOCALE);
require("cleave.js/dist/addons/cleave-phone.tr");
document.documentElement.setAttribute("lang", APP_LOCALE);

// Initializing App
const app = createApp(AppWrapper);

app
  .component("vue-select", VueSelect)
  .component("v-button", VButton)
  .component("v-icon", VIcon)
  .component("v-modal", VModal)
  .component("v-mask", VMask)
  .component("breadcrumb-list", BreadcrumbList)
  .component("validation-feedback", ValidationFeedback)
  .component("data-table", DataTable);

app
  .use(store)
  .use(router)
  .use(i18n)
  .use(Toast)

  .mount("#app");
