import Toast from "@/core/Toast";
import i18n from "@/lang/i18n";

const { t } = i18n.global;

export type FileResult = ArrayBuffer | string | null;

export type GetImageResponse = {
  file: File;
  imageData: FileResult;
};

export async function getImage(event: any): Promise<GetImageResponse> {
  const file = event.target.files[0];

  if (file) {
    if (!file.type.startsWith("image/")) {
      Toast.fire(t("errors.upload.file_must_be_image"), "error");

      return Promise.reject("FILE_MUST_BE_IMAGE");
    }

    const reader: FileReader = new FileReader();
    reader.readAsDataURL(file);
    return await new Promise(
      (resolve) =>
        (reader.onload = (e: ProgressEvent<FileReader>) =>
          resolve({
            file: file,
            imageData: e.target?.result ?? null,
          }))
    );
  }

  return Promise.reject("FILE_NOT_FOUND");
}
