import api from "@/core/Api";

export type SortDirection = "asc" | "desc";

class FetchRequest {
  public filters: Record<string, any> = {
    page: 1,
    limit: 15,
  };
  public listRoute: string;
  public filterActive: boolean = false;
  public pagination: Record<string, any> = {};
  public response: Record<string, any> = {};

  constructor(listRoute: string) {
    this.listRoute = listRoute;
  }

  getFilter(key: string, defaultValue: any = null): any {
    if (typeof this.filters[key] !== "undefined") {
      return this.filters[key];
    }

    return defaultValue;
  }

  setFilter(key: string, value: any): void {
    this.filters[key] = value;
  }

  getPage(): number {
    return this.filters.page;
  }

  setPage(page: number): void {
    this.filters.page = page;
  }

  getLimit(): number {
    return this.filters.limit;
  }

  setLimit(limit: number): void {
    this.filters.page = 1;
    this.filters.limit = limit;
  }

  getSort(): string {
    return this.filters.sort;
  }

  getSortType(): SortDirection {
    return this.filters.sort_type;
  }

  setSort(column: string, type: SortDirection): void {
    this.filters.page = 1;
    this.filters.sort = column;
    this.filters.sort_type = type;
  }

  getFilters(): Record<string, any> {
    let params: Record<string, any> = {};

    if (this.filterActive) {
      for (const filter in this.filters) {
        if (this.filters[filter] !== "" && this.filters[filter] !== null) {
          params[filter] = this.filters[filter];
        }
      }
    } else {
      params = {
        page: this.filters.page,
        limit: this.filters.limit,
        sort: this.filters.sort,
        sort_type: this.filters.sort_type,
      };
    }

    return { params: params };
  }

  activateFilters(): void {
    this.filters.page = 1;
    this.filterActive = true;
  }

  disableFilters(): void {
    this.filters.page = 1;
    this.filterActive = false;
  }

  fetch(uri: string|null = null): Promise<Object[]> {
    return new Promise((resolve, reject) => {
      api
        .get(uri ?? this.listRoute)
        .then((response) => {
          resolve(response.data);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }

  fetchAll(): Promise<Object[]> {
    return new Promise((resolve, reject) => {
      api
        .get(`${this.listRoute}/all`)
        .then((response) => {
          resolve(response.data);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }

  fetchWithFilters(): Promise<Object[]> {
    return new Promise((resolve, reject) => {
      api
        .get(this.listRoute, this.getFilters())
        .then((response) => {
          this.pagination = {
            ...response.data,
          };

          delete this.pagination["data"];

          resolve(response.data.data);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }
}

export default FetchRequest;
