import Model from "@/models/Model";
import Role from "@/models/Role";
import Hospital from "@/models/Hospital";
import Clinic from "@/models/Clinic";

class User extends Model {
  public name: string | null = null;
  public surname: string | null = null;
  public username: string | null = null;
  public email: string | null = null;
  public phone: number | null = null;
  public role_id: number | null = null;
  public hospital_id: number | null = null;
  public clinic_id: number | null = null;
  public deleted_at: number | null = null;

  constructor(data: Record<string, any> | null = null) {
    super();

    this.saveAttributes(data);
  }

  get role(): Role {
    return this.belongsTo(Role);
  }

  get hospital(): Hospital {
    return this.belongsTo(Hospital);
  }

  get clinic(): Clinic {
    return this.belongsTo(Clinic);
  }

  get fullName(): string {
    return `${this.name} ${this.surname}`;
  }
}

export default User;
