import Model from "@/models/Model";
import Group from "@/models/Group";
import { APP_URL } from "@/core/Config";
import i18n from "@/lang/i18n";
import { FileResult, getImage, GetImageResponse } from "@/helpers/File";

const { t } = i18n.global;

class Workspace extends Model {
  public user_id: number | null = null;
  public name: string | null = null;
  public logo: File | string | null = null;
  public groups: Group[] = [];

  constructor(data: Record<string, any> | null = null) {
    super();

    this.saveAttributes(data);
  }

  get groupsModel(): new () => Model {
    return Group;
  }

  get logoSrc(): string {
    return this.logo ? `${APP_URL}/images/workspaces/logos/${this.logo}` : "";
  }

  addGroup(data: Record<string, any> | null = null): Group {
    if (!data) {
      data = {
        name: `${t("titles.group")} ${this.groups.length + 1}`,
        size: 0,
      };
    }

    const group: Group = new Group(data);
    this.groups.push(group);
    return group;
  }

  removeGroup(index: number): void {
    this.groups.splice(index, 1);
  }

  async setLogoFile(event: any): Promise<FileResult> {
    const response: GetImageResponse | void = await getImage(event).catch(
      (error): void => {
        console.log(`Error while getting image: ${error}`);
      }
    );

    if (response !== null && typeof response === "object") {
      this.logo = response.file;

      return response.imageData;
    }

    return null;
  }
}

export default Workspace;
