<template>
  <!-- Breadcrumb Item -->
  <li v-if="active" key="breadcrumbItemCurrent">{{ title }}</li>
  <li
    v-else-if="route === null"
    key="breadcrumbItemHasNotRoute"
    class="flex items-center space-x-2"
  >
    <span>{{ title }}</span>

    <v-icon name="chevron-right" size="4"></v-icon>
  </li>
  <li v-else key="breadcrumbItem" class="flex items-center space-x-2">
    <router-link
      class="transition-colors text-primary hover:text-primary-focus dark:text-accent-light dark:hover:text-accent"
      :to="{ name: route }"
      >{{ title }}
    </router-link>

    <v-icon name="chevron-right" size="4"></v-icon>
  </li>
</template>

<script>

export default {
  name: "BreadcrumbListItem",
  props: {
    title: String,
    route: {
      type: String,
      default: null,
    },
    active: {
      type: Boolean,
      default: false,
    },
  },
};
</script>
