import User from "@/models/User";
import Role from "@/models/Role";

export default {
  state: () => ({
    user: localStorage.user ? JSON.parse(localStorage.getItem("user")) : null,
    token: localStorage.token ? localStorage.getItem("token") : null,
    userModel: null,
    roleModel: null,
  }),

  getters: {
    user: (state) => {
      if (state.user) {
        if (!(state.userModel instanceof User)) {
          state.userModel = new User({
            ...state.user,
            clinic: undefined,
            hospital: undefined,
            role: undefined,
          });
        }

        return state.userModel;
      }

      return null;
    },
    role: (state) => {
      if (state.user && state.user.role) {
        if (!(state.roleModel instanceof Role)) {
          state.roleModel = new Role(state.user.role);
        }

        return state.roleModel;
      }

      return null;
    },
    token: (state) => state.token,
    authenticated: (state) => state.token !== null,
    is_admin: (state) => state.user?.role?.is_admin ?? false,
  },

  mutations: {
    SET_DATA(state, data) {
      if (data === null) {
        state.user = null;
        state.token = null;

        localStorage.removeItem("user");
        localStorage.removeItem("token");

        return;
      }

      state.user = data.user;
      state.token = data.token;
      state.userModel = new User(data.user);
      state.roleModel = new Role(data.user.role);

      localStorage.user = JSON.stringify(data.user);
      localStorage.token = data.token;
    },
  },

  actions: {
    login({ commit }, data) {
      commit("SET_DATA", data);
    },

    logout({ commit }) {
      commit("SET_DATA", null);
    },
  },
};
