import Model from "@/models/Model";
import Question from "@/models/Question";
import QuestionType from "@/models/QuestionType";

class Template extends Model {
  public user_id: number | null = null;
  public name: string | null = null;
  public description: string | null = null;
  public questions: Question[] = [];

  constructor(data: Record<string, any> | null = null) {
    super();

    this.saveAttributes(data);
  }

  getIgnores(): string[] {
    return [...super.getIgnores(), "user_id"];
  }

  afterStore(): void {
    this.reOrder();
  }

  get questionsModel(): new () => Model {
    return Question;
  }

  addQuestion(question_type: QuestionType): void {
    this.questions.push(
      Question.generateFromType(question_type, this.questions.length + 1)
    );

    this.reOrder();
  }

  addRelatedQuestion(question: Question): void {
    this.questions.push(
      Question.generateRelated(question, this.questions.length + 1)
    );

    this.reOrder();
  }

  getRelatedQuestions(question: Question): Question[] {
    return this.questions.filter((quest: Question) => {
      return (
        quest.order === question.order ||
        (question.id !== null && quest.id === question.id) ||
        quest.related_order === question.order ||
        (question.id !== null && quest.related_to === question.id)
      );
    });
  }

  getOnlyRelatedQuestions(question: Question): Question[] {
    return this.questions.filter((quest: Question) => {
      return (
        quest.related_order === question.order ||
        (question.id !== null && quest.related_to === question.id)
      );
    });
  }

  removeQuestion(question: Question): void {
    if (question.isRelated) {
      this.questions = this.questions.filter((quest: Question) => {
        return (
          (quest.order !== question.order ||
            (question.id !== null && quest.id !== question.id)) &&
          (quest.related_order !== question.order ||
            (question.id !== null && quest.related_to !== question.id))
        );
      });
    } else {
      const index: number = this.questions.findIndex(
        (quest: Question): boolean => {
          return quest.order === question.order;
        }
      );

      this.questions.splice(index, 1);
    }

    this.reOrder();
  }

  getRenderAbleQuestions(): Question[] {
    return this.questions.filter((question: Question) => {
      return question.isRenderAble;
    });
  }

  reOrder(): void {
    const sortedQuestions: Question[] = [];

    this.questions.forEach((question: Question): void => {
      if (question.related_order === null && question.related_to === null) {
        sortedQuestions.push(question);
      }

      if (question.isRelated) {
        this.getOnlyRelatedQuestions(question).forEach(
          (relatedQuestion: Question): void => {
            sortedQuestions.push(relatedQuestion);
          }
        );
      }
    });

    let question_order: number = 1;

    sortedQuestions.forEach((question: Question, index: number) => {
      const newOrder: number = index + 1;

      if (question.isRelated) {
        sortedQuestions.forEach((quest: Question): void => {
          if (
            quest.related_order === question.order ||
            (question.id !== null && quest.related_to === question.id)
          ) {
            quest.related_order = newOrder;
          }
        });
      }

      question.order = newOrder;

      if (question.component && question.component.startsWith("Input")) {
        question.question_order = question_order;
        question_order++;
      }
    });

    this.questions = sortedQuestions;
  }

  removedQuestionType(questionTypeId: number, mainQuestionTypeId: number) {
    this.questions.forEach((question: Question) => {
      if (question.question_type_id === questionTypeId) {
        question.question_type_id = mainQuestionTypeId;
      }
    });
  }

  get questionCount(): number {
    return this.questions.filter(
      (question) => question.questionType.type === "input"
    ).length;
  }
}

export default Template;
