import Model from "@/models/Model";
import { Moment } from "moment";
import Survey from "@/models/Survey";
import Patient from "@/models/Patient";
import Answer from "@/models/Answer";
import Question from "@/models/Question";

class SurveyItem extends Model {
  public survey_id: number | null = null;
  public patient_id: number | null = null;
  public token: string | null = null;
  public sent_at: Moment | string | null = null;
  public completed_at: Moment | string | null = null;
  public filled_by_user: boolean | null = null;
  public answers: Answer[] = [];

  constructor(data: Record<string, any> | null = null) {
    super();

    this.saveAttributes(data);
  }

  get survey(): Survey {
    return this.belongsTo(Survey);
  }

  get patient(): Patient {
    return this.belongsTo(Patient);
  }

  get questionsModel(): new () => Model {
    return Question;
  }

  getCasts(): Record<string, string> {
    return {
      ...super.getCasts(),
      sent_at: "date",
      completed_at: "date",
    };
  }
}

export default SurveyItem;
