<template>
  <button
    type="button"
    class="transition-colors btn disabled:opacity-75 disabled:cursor-not-allowed"
    :class="buttonClasses"
  >
    <slot></slot>
  </button>
</template>

<script>
export default {
  name: "VButton",
  props: {
    color: {
      type: String,
      default: "primary",
    },
    size: {
      type: [String, Number],
      default: 9,
    },
    icon: {
      type: Boolean,
      default: false,
    },
    block: {
      type: Boolean,
      default: false,
    },
    align: {
      type: String,
      required: false,
    },
    weight: {
      type: String,
      default: "medium",
    },
  },
  computed: {
    buttonClasses() {
      return [
        {
          "text-slate-800 bg-slate-150 hover:bg-slate-200 focus:bg-slate-200 active:bg-slate-200/80 dark:bg-navy-500 dark:text-navy-50 dark:hover:bg-navy-450 dark:focus:bg-navy-450 dark:active:bg-navy-450/90":
            this.color === "default",
          "text-white bg-primary hover:bg-primary-focus focus:bg-primary-focus active:bg-primary-focus/90 dark:bg-accent dark:hover:bg-accent-focus dark:focus:bg-accent-focus dark:active:bg-accent/90":
            this.color === "primary",
          "text-white bg-secondary hover:bg-secondary-focus focus:bg-secondary-focus active:bg-secondary-focus/90":
            this.color === "secondary",
          "text-white bg-info hover:bg-info-focus focus:bg-info-focus active:bg-info-focus/90":
            this.color === "info",
          "text-white bg-success hover:bg-success-focus focus:bg-success-focus active:bg-success-focus/90":
            this.color === "success",
          "text-white bg-warning hover:bg-warning-focus focus:bg-warning-focus active:bg-warning-focus/90":
            this.color === "warning",
          "text-white bg-error hover:bg-error-focus focus:bg-error-focus active:bg-error-focus/90":
            this.color === "error" || this.color === "danger",
          "text-slate-400 hover:text-slate-800 focus:text-black active:text-black":
            this.color === "transparent",
        },
        this.sizeClass,
        this.iconClasses,
        this.blockClasses,
        this.alignClasses,
        this.weightClasses,
      ];
    },
    sizeClass() {
      return this.color !== "transparent" ? `h-${this.size}` : "";
    },
    iconClasses() {
      if (this.icon) {
        if (this.color === "transparent") {
          return "inline-block px-3 py-2";
        }

        return `w-${this.size} rounded-full p-0`;
      }

      return "";
    },
    blockClasses() {
      if (this.block) {
        if (this.color === "transparent") {
          return "block w-full px-0";
        }

        return "block w-full";
      }

      return "";
    },
    alignClasses() {
      const aligns = ["left", "center", "right", "start", "end", "justify"];

      return aligns.includes(this.align) ? `text-${this.align}` : "";
    },
    weightClasses() {
      const weights = [
        "thin",
        "extralight",
        "light",
        "normal",
        "medium",
        "semibold",
        "bold",
        "extrabold",
        "black",
      ];

      return weights.includes(this.weight) ? `font-${this.weight}` : "";
    },
  },
};
</script>