<template>
  <!-- Sidebar -->
  <div class="print:hidden sidebar">
    <!-- Main Sidebar -->
    <div class="main-sidebar">
      <div
        class="flex h-full w-full flex-col items-center border-r bg-white px-5 border-slate-150 dark:border-navy-700 dark:bg-navy-800"
      >
        <!-- Application Logo -->
        <div class="flex pt-4">
          <router-link
            :to="{ name: isAdmin ? 'admin.dashboard' : 'app.dashboard' }"
          >
            <img src="/logo.png" alt="logo" />
          </router-link>
        </div>

        <!-- Main Sections Links -->
        <div
          class="flex w-full grow flex-col overflow-y-auto pt-6 is-scrollbar-hidden space-y-3"
        >
          <admin-sidebar-menu v-if="isAdmin"></admin-sidebar-menu>
          <app-sidebar-menu v-else></app-sidebar-menu>
        </div>

        <!-- Bottom Links -->
        <div class="flex flex-col items-center py-3 space-y-3">
          <!-- Profile -->
          <div id="profile-wrapper" class="flex">
            <button id="profile-ref" class="h-12 w-12 avatar">
              <img
                class="rounded-full"
                src="/assets/images/200x200.png"
                alt="avatar"
              />
              <span
                class="absolute right-0 rounded-full border-2 border-white h-3.5 w-3.5 bg-success dark:border-navy-700"
              ></span>
            </button>
            <div id="profile-box" class="fixed popper-root">
              <div
                class="w-64 rounded-lg border bg-white popper-box border-slate-150 shadow-soft dark:border-navy-600 dark:bg-navy-700"
              >
                <div
                  class="flex items-center rounded-t-lg bg-slate-100 px-4 py-5 space-x-4 dark:bg-navy-800"
                >
                  <div class="h-14 w-14 avatar">
                    <img
                      class="rounded-full"
                      src="/assets/images/200x200.png"
                      alt="avatar"
                    />
                  </div>
                  <div>
                    <a
                      href="#"
                      class="text-base font-medium text-slate-700 hover:text-primary focus:text-primary dark:text-navy-100 dark:hover:text-accent-light dark:focus:text-accent-light"
                    >
                      {{ user.fullName }}
                    </a>
                    <p class="text-xs text-slate-400 dark:text-navy-300">
                      Product Designer
                    </p>
                  </div>
                </div>
                <div class="flex flex-col pt-2 pb-5">
                  <div class="mt-3 px-4">
                    <button
                      type="button"
                      class="h-9 w-full text-white btn space-x-2 bg-primary hover:bg-primary-focus focus:bg-primary-focus active:bg-primary-focus/90 dark:bg-accent dark:hover:bg-accent-focus dark:focus:bg-accent-focus dark:active:bg-accent/90"
                      @click="logout()"
                    >
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        class="h-5 w-5"
                        fill="none"
                        viewBox="0 0 24 24"
                        stroke="currentColor"
                      >
                        <path
                          stroke-linecap="round"
                          stroke-linejoin="round"
                          stroke-width="1.5"
                          d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1"
                        />
                      </svg>
                      <span>{{ $t("auth.logout") }}</span>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AdminSidebarMenu from "@/layout/admin/AdminSidebarMenu.vue";
import AppSidebarMenu from "@/layout/app/AppSidebarMenu.vue";

export default {
  name: "AppSidebar",
  components: { AppSidebarMenu, AdminSidebarMenu },
  data() {
    return {
      user: null,
      isAdmin: false,
    };
  },
  created() {
    this.user = this.$store.getters.user;
    this.isAdmin = this.$store.getters.is_admin;
  },
  methods: {
    logout() {
      this.$store.dispatch("logout");
      this.$router.push({ name: "login" });
    },
  },
};
</script>

<style>
.sidebar-menu-icon {
  @apply h-6 w-6;
}
</style>
